.list {
  list-style: none;
  font-size: 20px;
  text-align: center;
}
.item {
  padding-bottom: 10px;
}
.button {
  margin-left: 10px;
  padding: 6px 10px;
  border-radius: 25px;
  cursor: pointer;
}
.button:hover {
  color: blue;
  transform: scale(1.2);
  background-color: bisque;
}
